@import "../utils/variables.scss";
@import "../utils/mixins.scss";

//"contact me" button on photographer page
.button_contact {
  position: fixed;
  bottom: 4%;
  background-color: $color-primary;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  width: 170px;
  height: 35px;
  border-radius: 5px;
  border: none;
  @include desktop {
    position: static;
    align-self: flex-start;
    font-size: 20px;
    height: 70px;
    margin-top: 0.5rem;
  }
}

.button_banner:hover {
  background-color: $color-tertiary;
  color: #000000;
}

.submit_button {
  align-self: center;
  @include desktop {
    align-self: flex-start;
  }
}

//button like

.checkbox__like input,
.checkbox__like .like__checked {
  display: none;
}

.checkbox__input:checked ~ .like__checked {
  display: inline-block;
}

.checkbox__input:checked ~ .like__unchecked {
  display: none;
}

.far,
.fas {
  color: $color-primary;
  font-size: 13px;
  @include desktop {
    font-size: 20px;
  }
}
